/*---------------------------- CLOSE | MINMAX ----------------------------*/

.E_PageSectionSettings--min-max,
.E_PageSectionSettings--close {
    font-size: 23px;
    text-align: center;
    line-height: 24px;

    z-index: 110;
    position: absolute;
    top: 0px;
    right: 0px;

    width: 24px;
    height: 24px;

    color: #404040;
    background-color: transparent;
    border: none;
    opacity: 0.5;

    transition: var(--default-transition);
}

.E_PageSectionSettings--min-max {
    font-size: 14px;

    right: 24px;
}

@media (pointer: coarse) {

    .E_PageSectionSettings--close {
        font-size: 25px;
        text-indent: 4px;

        width: 32px;
        height: 32px;
        padding-bottom: 8px;
    }

    .E_PageSectionSettings--min-max {
        font-size: 17px;

        right: 32px;

        width: 32px;
        height: 32px;
    }
}

.E_PageSectionSettings--min-max__max {
    color: orange;
    opacity: 1;
}

.E_PageSectionSettings--min-max:hover,
.E_PageSectionSettings--close:hover {
    opacity: 1;

    transition: var(--default-transition-hover);
}

.E_PageSectionSettings--min-max:active,
.E_PageSectionSettings--close:active {
    transform: scale(0.95);

    transition: var(--default-transition-active);
}

/*---------------------------- RESIZER ----------------------------*/

.E_PageSectionSettings--resizer {
    z-index: 100;
    position: absolute;
    bottom: -7px;
    left: 0px;

    width: 100%;
    height: 12px;

    cursor: ns-resize;
}

@media (pointer: coarse) {

    .E_PageSectionSettings--resizer {
        bottom: -13px;

        height: 24px;
    }
}

.E_PageSectionSettings--resizer:after {
    content: "";

    position: absolute;
    top: calc(50% - 1px);
    left: 0px;

    width: 100%;
    height: 0px;

    border-bottom: 1px solid transparent;

    cursor: ns-resize;

    transition: var(--default-transition);

    transform: translateZ(0);
}

.E_PageSectionSettings--resizer:hover:after {
    border-bottom-color: red;

    transition: var(--default-transition-hover);
}

.E_PageSectionSettings--resizer__active:after,
.E_PageSectionSettings--resizer:active:after {
    border-bottom-color: orange;

    transition: var(--default-transition-active);
}

