    visibility: hidden;
}

/*--------------------------------------------------------------------*/
/*---------------------------- COLOR BOXES ---------------------------*/
/*--------------------------------------------------------------------*/

.ColorPicker--widget-wrapper {
    margin-bottom: 8px;
    margin-right: 24px;

    flex-basis: 240px;
    flex-shrink: 1;
    flex-grow: 0;

    display: flex;
    flex-wrap: wrap;
}

.ColorPicker--HSV-wrapper {
    font-size: 0;

    width: 100%;
}

/*---------------------------- BOXES ----------------------------*/

.ColorPicker--SV-box {
    vertical-align: top;

    position: relative;
    margin-bottom: 8px;

    width: calc(100% - 14px - 8px);
    height: 0px;
    padding-bottom: calc(100% - 8px - 14px - 1px - 1px);

    background-image:
        linear-gradient(
            to bottom,
            rgba(0, 0, 0, 0),
            rgba(0, 0, 0, 1)
        ),
        linear-gradient(
            to right,
            rgb(255, 255, 255),
            rgb(255,   0,   0)
        );
    border: 1px solid black;

    display: inline-block;
    will-change: background-image;
}

.ColorPicker--H-box {
    position: relative;
    margin-left: 8px;

    width: 14px;
    min-width: 14px;
    height: 0;
    padding-bottom: calc(100% - 8px - 14px - 1px - 1px);

    background-image:
        linear-gradient(
            to top,
            #ff0000   0.00%,
            #ffff00  16.67%,
            #00ff00  33.33%,
            #00ffff  50.00%,
            #0000ff  66.67%,
            #ff00ff  83.33%,
            #ff0000 100.00%
        );
    border: 1px solid black;

    display: inline-block;
}

.ColorPicker--H-box:before,
.ColorPicker--H-box:after {
    content: "";

    position: absolute;
    top: -1px;

    width: 9px;
    height: calc(100% + 2px);
}

.ColorPicker--H-box:before {
    left: -9px;
}

.ColorPicker--H-box:after {
    left: 12px;
}

/*---------------------------- SELECTORS ----------------------------*/

.ColorPicker--SV-selector,
.ColorPicker--H-selector {
    position: absolute;

    will-change: transform;
    transition: transform 0.35s cubic-bezier(.1, 1, .4, 1) 0s;
}

.ColorPicker--SV-selector {
    top: -6px;
    left: -6px;
    transform: translate(0px, 0px);

    width: 12px;
    height: 12px;

    border-radius: 50%;
    border: 1px solid red;
}

.ColorPicker--H-selector {
    top: -5px;
    left: -2px;
    transform: translateY(0px);

    width: 16px;
    height: 8px;

    border: 1px solid red;
}

/*--------------------------------------------------------------------*/
/*------------------------------ INPUTS ------------------------------*/
/*--------------------------------------------------------------------*/

.ColorPicker--input {
    width: 100%;
